.features {
  padding-top: 25px;
  padding-bottom: 40px;
}

.features__item {
  text-align: center;
}

.skills {
  padding-top: 40px;
  padding-bottom: 70px;
}

.skills__title {
  text-align: center;
  margin-bottom: 35px;
}

.skills__item {
  background-color: #3a0c50;
  padding: 40px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.skills__item-title {
  color: #ffffff;
  margin-bottom: 21px;
}

.skills__item-text {
  color: #ffffff;
}

.programm {
  padding-bottom: 93px;
}

.programm__title {
  margin-bottom: 42px;
  text-align: center;
  display: block;
  width: 100%;
}

.programm__item {
  position: relative;
  height: 76px;
}

.programm__modal-btn {
  margin: 0;
  padding: 0;
  background-color: transparent;
  box-shadow: none;
  border: 0;
  display: inline-block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  border: 2px dashed #bebebe;
  box-sizing: border-box;
  border-radius: 5px;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  /* or 120% */
  text-align: center;
  color: rgba(0, 0, 0, 0.85);
}

.programm__modal-btn:focus {
  outline: 0;
  box-shadow: none;
}

.programm__modal-btn:hover {
  border: 2px dashed #3a0c50;
  box-sizing: border-box;
  border-radius: 5px;
}

.programm__modal-btn:active {
  border: 2px dashed #a3189d;
  box-sizing: border-box;
  border-radius: 5px;
}

.programm__modal {
  display: none;
}

.programm__btn-block {
  margin-top: 60px;
  display: flex;
  justify-content: center;
}

.programm__btn {
  text-align: center;
}

.results {
  background: #e0e0e0;
  padding-top: 60px;
  padding-bottom: 90px;
}

.results__title {
  text-align: center;
  margin-bottom: 15px;
}

.results__item {
  text-align: center;
}

.organizations {
  padding-top: 80px;
  padding-bottom: 90px;
}

.organizations__title {
  text-align: center;
  margin-bottom: 50px;
}

.organizations__item {
  margin-bottom: 50px;
}

.form {
  background: #3a0c50;
  padding-top: 45px;
  padding-bottom: 100px;
}

.form__title {
  text-align: center;
  color: rgba(255, 255, 255, 0.85);
}

.form__desc {
  font-size: 18px;
  line-height: 24px;
  /* or 133% */
  color: rgba(0, 0, 0, 0.85);
}

.form__right {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form__step {
  background: #ffffff;
  border-radius: 5px;
  padding: 50px;
  padding-top: 30px;
  position: relative;
}

.form__step h3 {
  font-weight: bold;
  font-size: 32px;
  line-height: 64px;
  /* or 200% */
  color: rgba(0, 0, 0, 0.85);
  text-align: center;
}

.form__step-1 .form__step-number {
  left: 50px;
}

.form__step-2, .form__step-3 {
  padding: 40px;
  padding-left: 57px;
  margin-bottom: 20px;
}

.form__step-2 h3, .form__step-3 h3 {
  line-height: 40px;
  text-align: left;
  margin-bottom: 30px;
}

.form__step-number {
  font-weight: bold;
  font-size: 32px;
  line-height: 64px;
  /* or 200% */
  color: #000000;
  position: absolute;
  left: 30px;
  top: 0;
}

.form-block {
  position: relative;
  padding-top: 25px;
}

.form-block__row {
  display: flex;
}

.form-block__item {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 50%;
  padding-right: 20px;
  margin-bottom: 40px;
}

.form-block__item label {
  font-size: 18px;
  line-height: 24px;
  /* identical to box height, or 133% */
  color: rgba(0, 0, 0, 0.85);
}

.form-block__item input,
.form-block__item select {
  display: block;
  width: 100%;
  height: 40px;
}

.form-block__item--last {
  width: 100%;
}

.form-block__btn-block {
  display: flex;
  justify-content: center;
  padding-top: 27px;
}

.form-block__privacy {
  font-size: 12px;
  line-height: 12px;
  /* or 100% */
  color: #737373;
}

.form-block__privacy a {
  color: #737373;
  font-size: 12px;
  line-height: 12px;
}

.form-block__privacy a:hover {
  text-decoration: none;
}

.partners {
  padding-top: 70px;
  padding-bottom: 36px;
}

.partners__title {
  text-align: center;
  color: rgba(0, 0, 0, 0.85);
  margin-bottom: 34px;
}

.partners__gallery {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.partners__gallery div {
  margin: 30px;
}

.faq {
  padding-bottom: 85px;
}

.faq__title {
  text-align: center;
  margin-bottom: 42px;
}

.faq__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.faq__item {
  margin-bottom: 10px;
  position: relative;
  height: 76px;
}

.faq__item-btn {
  text-align: left;
  margin: 0;
  padding: 0;
  background-color: transparent;
  box-shadow: none;
  border: 0;
  display: inline-block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: auto;
  width: 100%;
  height: 100%;
  border: 2px solid #bebebe;
  box-sizing: border-box;
  border-radius: 5px;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  padding-left: 25px;
  padding-right: 25px;
  color: rgba(0, 0, 0, 0.85);
}

.faq__item-btn:focus {
  outline: 0;
  box-shadow: none;
}

.faq__item-btn:hover {
  border: 2px solid #3a0c50;
  box-sizing: border-box;
  border-radius: 5px;
}

.faq__item-btn:active {
  border: 2px solid #a3189d;
  box-sizing: border-box;
  border-radius: 5px;
}

.faq__item-content {
  display: none;
}

.contacts {
  background: #c4c4c4;
  padding-top: 50px;
  padding-bottom: 100px;
}

.contacts a {
  font-size: 18px;
  color: rgba(0, 0, 0, 0.85);
}

.contacts a:hover {
  text-decoration: none;
}

.contacts__title {
  text-align: center;
  margin-bottom: 40px;
}

.contacts__profile-img {
  border-radius: 50%;
  width: 220px;
  height: 220px;
  overflow: hidden;
  -webkit-mask-image: -webkit-radial-gradient(white, black);
  margin-left: 20px;
  margin-bottom: 30px;
}

.contacts__profile-name {
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  /* or 120% */
  color: #000000;
  margin-bottom: 0;
}

.contacts__profile-role {
  font-size: 18px;
  line-height: 24px;
  /* identical to box height, or 133% */
  color: #000000;
}

.contacts__address {
  font-size: 18px;
  line-height: 24px;
  /* or 133% */
  color: rgba(0, 0, 0, 0.85);
}

.contacts__map {
  width: 730px;
  height: 448px;
  border: 1px solid #000;
  background: #c4c4c4;
  border-radius: 5px;
  margin-bottom: 20px;
}

.page-footer {
  background: #3a0c50;
  padding-top: 80px;
  padding-bottom: 80px;
}

.page-footer__logo {
  left: -10px;
  top: -10px;
  position: relative;
  margin-bottom: 20px;
}

.page-footer ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page-footer a:hover {
  color: #eda539;
  text-decoration: none;
  transition: 300ms ease-in-out;
}

.page-footer__phone, .page-footer__mail, .page-footer__address {
  display: block;
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 20px;
  color: rgba(255, 255, 255, 0.85);
}

.page-footer__about-link {
  margin-bottom: 30px;
  display: inline-block;
}

.page-footer__socials {
  display: flex;
}

.page-footer__socials-item a svg {
  width: 32px;
  height: 32px;
}

.page-footer ul.page-footer__nav--2 {
  padding-top: 55px;
}

.page-footer ul.page-footer__nav li {
  margin-bottom: 18px;
}

.page-footer__nav-title {
  margin-bottom: 30px;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  /* or 120% */
  color: rgba(255, 255, 255, 0.85);
}
